/*指引整体*/
@-webkit-keyframes guide {
0% {
    opacity: 1;
}
25% {
    opacity: 1;
}
50% {
    opacity: 1;
}
51% {
    opacity: 0;
}
75% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}
@keyframes guide {
0% {
    opacity: 1;
}
25% {
    opacity: 1;
}
50% {
    opacity: 1;
}
51% {
    opacity: 0;
}
75% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}
/*小手*/
@-webkit-keyframes hands {
0% {
    background: url(../../images/redbag-hand2.png) no-repeat center/contain;
}
100% {
    background: url(../../images/redbag-hand1.png) no-repeat center/contain;
}
